import React from "react"
import Layout from "../elements/layout"
import Container from "../elements/container/container"
import Header from "../elements/header/header"
import IllustrationBackgroundModern from "../background/background_3/backgroundmodern"
import SEO from "../elements/seo"
import Seperator from "../elements/seperator/seperator"
import CTA from "../elements/cta/cta"
import ShiftGrid from "../elements/shiftgrid"
import Example from "../elements/example/example"
import {graphql} from "gatsby"
import _ from "underscore"
import translate from "../translate"
import Examples from "../components/examples";
import {i18n} from "../i18n/language";

const BeispielePage = ({
                           data: {
                               allDirectory: {edges},
                           },
                       }) => {

    console.log(edges);
    var projects = edges
        .filter(edge => edge.node.absolutePath.indexOf('/projekt/') !== -1)
        .map(edge => GET_PROJECT_INFO_EDGE(edge.node.name))
        .filter(project => {
            return project.type === "project"
        })

    projects = _.sortBy(projects, function (o) {

        if (o.position) {
            return new Date();
        }
        return new Date(o.date)
    }).reverse()


    return <Layout>

        <SEO title={i18n.examples.meta.title}
             description={i18n.examples.meta.description}/>

        <Header title={i18n.examples.header.title}
                href={"#container_test"}
                subtitle={i18n.examples.header.subtitle}/>

        <IllustrationBackgroundModern>


            <Container id="container_test">
                <Examples projects={projects}/>
            </Container>


            <Seperator/>

            <CTA cta={{
                title: i18n.examples.cta.title,
                description: i18n.examples.cta.description,
                contact: i18n.examples.cta.contact,
                image: "https://webbakers.de/static/team-member-01-6fe955869498f39e33bd4f8bb622187c.jpg"
            }}/>

            <Seperator/>


        </IllustrationBackgroundModern>


    </Layout>
}

export const GET_PROJECT_INFO = (id) => {
    return require(`./../projekt/${id}/config.json`)
}

export const GET_PROJECT_INFO_EDGE = (id) => {
    //var id = require("path").basename(path).split(".").slice(0, -1).join(".");
    var project = GET_PROJECT_INFO(id);
    project.id = id;
    return project;
}

export default BeispielePage

export const pageQuery = graphql`
  query {
    allDirectory(sort: { order: DESC, fields: [] }) {
      edges {
        node {
          name,
          absolutePath
        }
      }
    }
  }
`


