import React, {useState} from "react"
import "./style.css"
import Button from "../../elements/button/button"
import Toastify from "toastify-js"
import "toastify-js/src/toastify.css"
import {i18n} from "../../i18n/language"
import Example from "../../elements/example/example";
import "./style.css";
import Seperator from "../../elements/seperator/seperator";


const Examples = ({projects}) => {

    const [type, setType] = useState(null);
    const [category, setCategory] = useState(0);

    const styles = require('./../../styles');
    return <div className={"exampleswrapper"}>
        <div className={"filterbuttons"}>
            <select value={category} onChange={(e) => {
                setCategory(parseInt(e.target.value,10))
            }}>
                <option value={0}>Stil wählen &nbsp; &nbsp; ▼</option>
                <option value={1}>Animierte Videos &nbsp; &nbsp; ▼</option>
                <option value={2}>Realdrehvideos &nbsp; &nbsp; ▼</option>
            </select>
            {category === 1 && <Button onClick={() => setType(null)} disabled={type !== null}>Alle</Button>}
            {category === 1 &&
            <Button onClick={() => setType(styles["1"].id)} disabled={type !== styles["1"].id}>Budget</Button>}
            {category === 1  &&
            <Button onClick={() => setType(styles["2"].id)} disabled={type !== styles["2"].id}>Premium</Button>}
            {category === 1 &&
            <Button onClick={() => setType(styles["3"].id)} disabled={type !== styles["3"].id}>Enterprise</Button>}
        </div>

        <div className={"videobakersexamples"}>
            {projects.map(project => {



                var visible = category === 0 || (category === 1 && project.style !== 4 && (type === null ||  type === project.style)) || (category === 2 && project.style === 4);

                return <Example hidden={!visible} project={project.id}/>
            })}
        </div>
    </div>
}

export default Examples